var statusBarHeight = uni.getSystemInfoSync().statusBarHeight + 44 + 'px'
var screenHeight = uni.getSystemInfoSync().screenHeight + 'px'
export default {
    
	data() {
		return {
			statusBarHeight: statusBarHeight,
            screenHeight:screenHeight,
            url:'',
            recognizeState:0,
            uploadParams:null,
            slogan:'分析中...',
            btnTitle:'查看报告',
            communityId:'',
            faceRecord:null
		}
	},
	onLoad(options) {
        this.url = options.url
        this.communityId = options.communityId
        const data = {
        	purpose: 'useravatar'
        }
        this.$api.getUploadParams({
        	data: data
        }).then(res => {
        	console.log('getUploadParams')
        	console.log(res)
        	this.uploadParams = res.data
            this.next()
        })
        
	},
	methods: {
        clickBtn:function(){
            if(this.recognizeState == 1){        //跳转报告
                uni.navigateTo({    
                    url:'../../../index/skinPos?sid='+this.faceRecord.id
                })
            }
            if(this.recognizeState == 2){
                uni.navigateBack({
                    delta:2
                })
            }
        },
        clickBack:function(){
            uni.navigateBack({
                delta:2
            })
        },
        next:function(){
            if (this.uploadParams) {
              
            	uni.showLoading({
            		title: '图片上传中'
            	})
                var _self = this
            	uni.uploadFile({
            		url: this.uploadParams.url,
            		filePath: this.url,
            		name: 'userAvatar',
            		formData: {
            			policy: this.uploadParams.policy,
            			Signature: this.uploadParams.Signature
            		},
                    success(result) {
            			if (result.data) {
                            console.log(result.data);
            				let r = JSON.parse(result.data)
            				if (r.status === 'OK') {
                                var pic = r.data.location + r.data.filepath
                                _self.recognize(pic)
                                
                    		} else {
                                uni.showToast({
                                    title: r.message,
                                    icon: "none",
                                    duration: 2000
                                })
                            }
                        }
                    },fail() {
                    		                
                    },complete(res) {
                    	uni.hideLoading()
                    	if (res.statusCode !== 200) {
                    		uni.showToast({
                    			title: res.errMsg,
                    			icon: "none",
                    			duration: 2000
                    		})
                    	}
                    }
                })
          
            }else{
                uni.showToast({
                    title: 'getUploadParams failed!',
                    icon: "none",
                    duration: 2000
                })
            }
        },
        recognize:function(url){
            
            const data = {
            	path: url,
                communityId:this.communityId
            }
            this.$api.faceAnalysis({
                data:data
            }).then(res=>{
                if(res.status == 'OK'){
                    this.faceRecord = res.data
                    this.recognizeState = 1
                    this.slogan = '分析完成！'
                    this.btnTitle = '查看报告'
                }else{
                    this.recognizeState = 2
                    this.slogan = '图片拍摄不符合要求，请按照示例拍摄！'
                    this.btnTitle = '重新拍摄'
                }
            })
            
        },
        moveHandle:function(){
            
        }
    },
}
